export const styles = css`[part=track],[part=inactive]{height:.5rem}[part=fill]{display:none}[part=inactive]{background:var(--base-track-color)}[part=steps]{color:var(--track-step-color)}[part~=thumb]{width:1rem;height:1rem;background:var(--thumb-color);top:calc((1rem - calc(1rem / 2))*-1);-webkit-margin-start:calc((1rem - calc(1rem / 2))*-1);margin-inline-start:calc((1rem - calc(1rem / 2))*-1)}[part~=thumb]:focus{border:.0625rem solid var(--thumb-border-focus-color);-webkit-box-shadow:0 0 0 .1875rem var(--thumb-focus-color);box-shadow:0 0 0 .1875rem var(--thumb-focus-color)}[part=tick]{background:var(--tick-color)}[part=tick-label]{color:var(--tick-label-color)}[part=thumb-label-inner]{color:var(--label-text-color);background:var(--label-background-color)}[part=thumb-label-inner]::after{border-top:.625rem solid var(--label-background-color)}:host([disabled]) [part=inactive],:host([disabled]) [part=tick],:host(:disabled) [part=inactive],:host(:disabled) [part=tick]{background:var(--disabled-base-track-color)}:host([disabled]) [part~=thumb],:host(:disabled) [part~=thumb]{background:var(--disabled-thumb-color)}:host([disabled]) [part=tick-label],:host(:disabled) [part=tick-label]{color:var(--disabled-base-track-color)}`;
